@import 'components';
@import 'base';
@import 'layout';

   
section{
    align-items: baseline;
    background-color: #C7A98A;
    border: solid 1px black;
    border-radius: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 1%;
    padding: 8%;
    div{
        align-content: center;
        text-align: center;
        width: 200px;
        a{
            color: black;
            text-decoration: none;
        }
    }
    img{
        height: 497px;
        width: 200px;
    }
}

.overlay {
    background: rgba(0, 0, 0, 0.7);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transition: opacity 500ms;
    visibility: hidden;
}
.overlay:target {
    opacity: 1;
    visibility: visible;
}
#popupBody{
    background: #C7A98A;
    border-radius: 15px;
    bottom: 30%;
    box-shadow: 0 0 5px #7D5A35;
    font-size: 20px;
    margin: 20% auto;
    padding: 2%;
    position: relative;
    transition: all 5s ease-in-out;
    width: 20%;
    .sendClean{
        input{
            height: 100%;
            width: 100%;
        }
    }
    input{
        height: 20px;
        width: 100%;
    }
    .buttonSend {
        background-color: #4CAF50;
        border: none;
        color: white;
        display: inline-block;
        font-size: 16px;
        margin: 4px 2px;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        width: 48%;
    }
    .buttonClean{
        background-color: #F00;
        border: none;
        color: white;
        display: inline-block;
        font-size: 16px;
        margin: 4px 2px;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        width: 48%;
    }    
}
#cerrar{
    color: #F00;
    font-size: 30px;
    font-weight: bold;
    position: absolute;
    right: 30px;
    text-decoration: none;
    top: 20px;
    transition: all 200ms;
}
